<template>
  <div id="course" style="border-radius: 5px">
    <el-tabs v-model="activeName">
      <el-tab-pane label="我学的课" name="first">
        <study-course></study-course>
      </el-tab-pane>
      <el-tab-pane label="我教的课" name="second">
        <professor-course></professor-course>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import StudyCourse from "./StudyCourse.vue";
import ProfessorCourse from "./ProfessorCourse.vue";
export default {
  name: "Course",
  components:{
    StudyCourse,
    ProfessorCourse
  },
  data() {
    return {
      activeName: 'first'
    };
  },
}
</script>

<style scoped>
#course {
  height: auto;
  width: auto;
  padding: 20px;
  background: white;
}
</style>



